import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/views/home'
import Admin from '@/views/admin'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },{
      path: '/admin',
      name: 'Admin',
      component: Admin
    }
  ]
})



